import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../css/Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import temp from "./comp_img/castorix_img/castorix_temp.png";  
import sohly_tair from "./comp_img/castorix_img/sohly_tair.png";
import the_north_star from "./comp_img/castorix_img/the_north_star.png";

class Castorix_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };
    const slideImages = [
      temp, sohly_tair, the_north_star
    ];
    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} alt="sample" />
                <div className="caption">
                  {(() => {
                    if (each == temp) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Placeholder</b></p>
                              <p className="caption_description">
                                text
                              </p>
                          </div>
                        </div>
                      )
                      } else if (each == sohly_tair) {
                        return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Sohly Tair</b></p>
                              <p className="caption_description">
                                text  
                              </p>
                          </div>
                        </div>
                        )
                      } else if (each == the_north_star) {
                        return (
                          <div className="sector_img_caption">
                            <div className="caption_padding">
                              <p className="caption_title"><b>The North Star</b></p>
                                <p className="caption_description">
                                  The capital of Castorix, a lively city that stands at the entrance to the Underground. Leading into the cities within the Winter Sector’s infamous onyx mountains, one must remember to always keep their NightSight Elixir close…
                                </p>
                            </div>
                          </div>
                        )
                      }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Castorix_Gallery;