import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../css/Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import temp from "./comp_img/alcerium_img/alcerium_temp.png";  
import karable from "./comp_img/alcerium_img/karable.png";
import depths_of_valerim from "./comp_img/alcerium_img/depths_of_valerim.png";

class Alcerium_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      easing: "ease",
      pauseOnHover: true,
      indicators: (i) => <div className="indicator"></div>
    };
    
    const slideImages = [
      temp,
      karable,
      depths_of_valerim
    ];

    return (
      <div className="App" >
        <div className="slide-container" >
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
                <div key={index} className="each-slide" >
                    <img className="lazy" src={each} alt="" />
                    <div className="caption">
                        {(() => {
                            if (each == temp) {
                                return (
                                    <div className="sector_img_caption">
                                        <div className="caption_padding">
                                            <p className="caption_title"><b>Placeholder</b></p>
                                            <p className="caption_description">
                                                text                                        
                                            </p>
                                        </div>
                                    </div>
                                )
                            } else if (each == karable) {
                                return (
                                    <div className="sector_img_caption">
                                        <div className="caption_padding">
                                            <p className="caption_title"><b>Karable</b></p>
                                            <p className="caption_description">
                                                The largest port linking Alcerium’s rainforest and ocean territory, serving as a resource center for those wishing to cross to land or take the Tubes into the Undersea.
                                            </p>
                                        </div>
                                    </div>
                                )
                            } else if (each == depths_of_valerim) {
                                return (
                                    <div className="sector_img_caption">
                                        <div className="caption_padding">
                                            <p className="caption_title"><b>Depths of Valerim</b></p>
                                            <p className="caption_description">
                                                What was once a beautiful, bustling underwater castle village ruled by royal merfolk is now a wasted wreckage sitting mostly forgotten at the bottom of the sea.
                                            </p>
                                        </div>
                                    </div>
                                )
                            }
                        })()}
                    </div>
                </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Alcerium_Gallery;