import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../css/Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import kozen_shrine from "./comp_img/lysande_img/kozen_shrine.png";  
import port_of_royals from "./comp_img/lysande_img/port_of_royals.png";
import purblinde from "./comp_img/lysande_img/purblinde.png";

class Lysande_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };
    
    const slideImages = [
      kozen_shrine, 
      port_of_royals, 
      purblinde
    ];

    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} figcaption="sample" />
                <div className="caption">
                  {(() => {
                    if (each == kozen_shrine) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Kozen Shrine</b></p>
                              <p className="caption_description">
                                A floating abode where the purest of hearts maintain the flow of moral enchantments in Lysande and located between the border of Lysande and Lyrisius.                                         
                              </p>
                          </div>
                        </div>
                      )
                      } else if (each == port_of_royals) {
                        return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Port of Royals</b></p>
                              <p className="caption_description">
                                Lysande’s capital port city is the world’s largest transportation intersection and is never seen without boisterous activity from day to night.
                              </p>
                          </div>
                        </div>
                        )
                      } else if (each == purblinde) {
                        return (
                          <div className="sector_img_caption">
                            <div className="caption_padding">
                              <p className="caption_title"><b>Purblinde District</b></p>
                                <p className="caption_description">
                                  An illegal red light district, where black market sales and brothels are around every corner. How the district continues to flourish without government interference is a mystery.
                                </p>
                            </div>
                          </div>
                        )
                      }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Lysande_Gallery;