import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../css/Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import havensworth from "./comp_img/lyrisius_img/havensworth.png";  
import the_elite_court from "./comp_img/lyrisius_img/the_elite_court.png";
import zeronyn from "./comp_img/lyrisius_img/zeronyn.png";

class Lyrisius_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };

    const slideImages = [
        havensworth, 
        the_elite_court, 
        zeronyn
    ];

    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} alt="sample" />
                <div className="caption">
                  {(() => {
                    if (each == havensworth) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Havensworth</b></p>
                              <p className="caption_description">
                                Lyrisius’ most renowned feature, the countless floating islands that make up a majority of middle and upper-class housing and entertainment.
                              </p>
                          </div>
                        </div>
                      )
                      } else if (each == the_elite_court) {
                        return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>The Elite Court</b></p>
                              <p className="caption_description">
                                A series of isolated floating palaces in Havensworth above the capitol city, Autu, and home to Lyrisius’ most elite houses: Aver, Chevalier, Fyvira, Paldeu and Wysterié. 
                              </p>
                          </div>
                        </div>
                        )
                      } else if (each == zeronyn) {
                        return (
                          <div className="sector_img_caption">
                            <div className="caption_padding">
                              <p className="caption_title"><b>Zeronyn</b></p>
                                <p className="caption_description">
                                  The ground terrain of Lyrisius, occupied by Lyrisius’ lower and working class, though frequented by all for its autumnal scenery and access to transportation.
                                </p>
                            </div>
                          </div>
                        )
                      }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Lyrisius_Gallery;