import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../css/Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import excelia_academy from "./comp_img/valterre_img/excelia_academy.png";  
import valterre_temp from "./comp_img/valterre_img/valterre_temp.png";
import arkenal from "./comp_img/valterre_img/arkenal.png";

class Valterre_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };
    const slideImages = [
        excelia_academy, 
        valterre_temp, 
        arkenal
    ];
    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} alt="sample" />
                <div className="caption">
                  {(() => {
                    if (each == excelia_academy) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Excelia</b></p>
                              <p className="caption_description">
                                The heart of Koranin and the pride of Valterre. The city is run by high-ranking officials and serves as the capital of all sectors. But to the common folk, it is the number one region for tourism and education, as it is home to the esteemed Excelia Academy. 
                              </p>
                          </div>
                        </div>
                      )
                      } else if (each == valterre_temp) {
                        return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Placeholder</b></p>
                              <p className="caption_description">
                                text  
                              </p>
                          </div>
                        </div>
                        )
                      } else if (each == arkenal) {
                        return (
                          <div className="sector_img_caption">
                            <div className="caption_padding">
                              <p className="caption_title"><b>Arkenal Prison</b></p>
                                <p className="caption_description">
                                    A menacing spiral tower that serves as the most heavily guarded prison in Valterre, graced with the presence of Koranin’s most villainous minds.
                                </p>
                            </div>
                          </div>
                        )
                      }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Valterre_Gallery;